'use client'

import '@/styles/history/history.css'
import '@/styles/history/historylist.css'
import Image from 'next/image'
import React, { useEffect } from 'react'

const History = () => {
  useEffect(() => {
    if (typeof window !== 'undefined') {
      import('gsap').then(({ default: gsap }) => {
        import('gsap/ScrollTrigger').then(({ ScrollTrigger }) => {
          gsap.registerPlugin(ScrollTrigger)

          let sections = gsap.utils.toArray<HTMLElement>('.panel')
          let container = document.querySelector<HTMLElement>('.container')

          function updateScrollAnimation() {
            requestAnimationFrame(() => {
              if (!container) return
              let totalWidth = sections.reduce(
                (acc, section) => acc + section.getBoundingClientRect().width,
                0
              )
              container.style.width = `${totalWidth}px`

              // 기존 ScrollTrigger 제거 후 다시 설정
              ScrollTrigger.getAll().forEach((trigger) => trigger.kill())

              gsap.to(container, {
                x: () => -(totalWidth - window.innerWidth),
                ease: 'none',
                scrollTrigger: {
                  trigger: '.container',
                  pin: true,
                  scrub: 1,
                  snap: {
                    snapTo: (progress) => {
                      let sectionOffsets = sections.map(
                        (s) => s.offsetLeft / totalWidth
                      )
                      return gsap.utils.snap(sectionOffsets, progress)
                    },
                    duration: 0.5,
                  },
                  end: () => `+=${totalWidth - window.innerWidth}`,
                },
              })
            })
          }

          // 페이지 로드 후 실행
          window.addEventListener('load', () => {
            setTimeout(updateScrollAnimation, 100)
          })
          window.addEventListener('resize', updateScrollAnimation)
        })
      })
    }
  }, [])

  return (
    <section className="sec history blue">
      <article className="secInner container">
        <div className="titleArea">
          <Image
            src="/images/section/HISTORY.svg"
            width={693}
            height={158}
            alt="HISTORY"
          />
          <h2 className="secTitle">創業から10年間の軌跡</h2>
        </div>

        {/* 2015 */}
        <div className="yeaer2015 panel">
          <figure className="yearImg">
            <img src="/images/section/historySection/2015.svg" alt="2015" />
          </figure>
          <div className="historyArea up">
            <div className="historybox">
              <i className="maruten"></i>
              <div className="textArea">
                <p className="month">
                  4<span>月</span>
                </p>
                <p className="text">
                  初月黒字化
                  <br />
                  1人目の社員入社
                </p>
              </div>
              <img
                src="/images/section/historySection/2015_1.png"
                alt="初月黒字化1人目の社員入社"
              />
            </div>
            <div className="historybox">
              <i className="maruten"></i>
              <div className="textArea">
                <p className="month">
                  9<span>月</span>
                </p>
                <p className="text">
                  TFイベントで決勝
                  <br />
                  エンジェル投資
                </p>
              </div>
              <img
                src="/images/section/historySection/2015_2.png"
                alt="TFイベントで決勝エンジェル投資"
              />
            </div>
          </div>
          <div className="historyArea down">
            <div className="historybox">
              <i className="maruten"></i>
              <div className="textArea">
                <p className="month">
                  6<span>月</span>
                </p>
                <p className="text">
                  3ヶ月で
                  <br />
                  競合企業に転職
                </p>
              </div>
              <img
                src="/images/section/historySection/2015_3.png"
                alt="3ヶ月で競合企業に転職"
              />
            </div>
          </div>
        </div>

        {/* 2016 */}
        <div className="yeaer2016 panel">
          <figure className="yearImg">
            <img src="/images/section/historySection/2016.svg" alt="2016" />
          </figure>
          <div className="historyArea up">
            <div className="historybox">
              <i className="maruten"></i>
              <div className="textArea">
                <p className="month">
                  6<span>月</span>
                </p>
                <p className="text">
                  Bizlink β版
                  <br />
                  ローンチ
                </p>
              </div>
              <img
                src="/images/section/historySection/2016_1.png"
                alt="Bizlink β版ローンチ"
              />
            </div>
          </div>
          <div className="historyArea down"></div>
        </div>

        {/* 2017 */}
        <div className="yeaer2017 panel">
          <figure className="yearImg">
            <img src="/images/section/historySection/2017.svg" alt="2017" />
          </figure>
          <div className="historyArea up">
            <div className="historybox">
              <i className="maruten"></i>
              <div className="textArea">
                <p className="month">
                  8<span>月</span>
                </p>
                <p className="text">
                  IT導入補助金爆発
                  <br />
                  クラウドファンディング爆発
                </p>
              </div>
              <img
                src="/images/section/historySection/2017_1.png"
                alt="IT導入補助金爆発クラウドファンディング爆発"
              />
            </div>
          </div>
          <div className="historyArea down"></div>
        </div>
      </article>
    </section>
  )
}

export default History
